.poke-font {
  font-family: "pokemon-font";
}


/* NAVIGATION */
@media screen and (max-width: 375px) {
  .app-wrapper {
    max-width: 100vw;
  }

  nav ul {
    flex-wrap: wrap;
  }
  
  nav ul li {
    padding-top: 10px;
  }
}


/*POKEMON LIST*/
.pokemons-list-wrapper, .pokemons-fav-list-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 2fr));
  margin: 30px 0;
  text-align: center;
}

.pokemons-list-wrapper,.pokemons-fav-list-wrapper {
  gap: 20px;
}

/*POKEMON CARD FROM A LIST*/
.pokemon-card {
  background-color: white;
  transition: color 0.3s, background-color 0.3s;
}

.pokemon-card:hover {
  color: white;
}

/*POKEMON PROFILE CARD*/
.pokemon-details-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  padding: 25px;
  border-radius: 20px;
}

.pokemon-details-card img {
  margin: 30px;
  min-width: 300px;
  min-height: 300px;
  object-fit: cover;
}

.pokemon-details-card-text h3 {
  margin-bottom: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}

.pokemon-details-card-text p {
  text-align: center;
  padding: 10px 0;
}

.pokemon-details-types-info, .pokemon-details-abilities-info {
  text-align: center;
  margin: 10px;
}

.pokemon-details-types-info span, .pokemon-details-abilities-info span {
  margin: 10px;
  padding: 10px;
  border-radius: 12px;
}

.pokemon-details-types-info span {
  background-color: purple;
}

.pokemon-details-abilities-info span {
  display: inline-block;
  background-color: rgb(35, 167, 167);
}

.pokemon-details-title {
  display: flex;
}

.pokemon-details-title button {
  margin-left: 20px;
}

/*PAGINATION BUTTONS*/
.pagination-buttons-wrapper {
  justify-content: space-evenly;
  margin-top: 30px;
}

.pagination-buttons-wrapper button {
  text-transform: uppercase;
}
